@import './AppConstants';

@mixin flexWrap {
  display: flex;
}

@mixin buttonStyle {
  cursor: pointer;
  width: $btn-size;
  height: $btn-size;
  transition: color 0.3s linear;
}

body {
  margin: 0;
  font-family: $font;
}

.centered-container {
  display: flex;
  align-items: center;
  justify-content: center;
  flex: 100%;
  margin-top: 1em;
}

.page-title {
  display: flex;
  justify-content: center;

  @media (max-width: 500px) {
    flex-direction: column;
  }

  .page-h {
    @media (max-width: 850px) {
      font-size: 1.1rem;
      line-height: 80px;
      margin-right: 20px;
    }

    @media (max-width: 500px) {
      line-height: 20px;
    }

    line-height: 50px;
    margin-right: 10px;
  }
}

.under-line {
  position: relative;

  &::after {
    content: '';
    position: absolute;
    left: 0;
    width: 100%;
    bottom: -10px;
    border-bottom: 1px solid;
  }
}

.container {
  max-width: 1440px;
  margin: 10px auto;

  @media (max-width: 1440px) {
    margin: 10px 10px;
  }
}

.form-fields-container {
  @include flexWrap();
  align-items: center;
  justify-content: space-between;

  .MuiFormControl-root,
  .form-field {
    width: fit-content;
    padding-right: 24px;
  }

  label + .MuiInput-formControl {
    padding-right: 0;
  }

  .MuiTypography-root,
  span.MuiCheckbox-root svg {
    margin-top: 16px;
  }
}

.form-buttons-container {
  @include flexWrap();
  align-items: center;
  justify-content: space-between;
  margin-top: 10px;

  .buttons-style {
    width: 48%;

    @media (max-width: 768px) {
      margin-bottom: 5px;
      font-size: 12px;
      width: 60%;
    }
  }
}

.margin-top-bottom {
  margin: 2px 0 7px 0;
}

.cards-container {
  margin-bottom: 50px;
  @media (max-width: 850px) {
    flex-direction: column;
    align-items: center;
  }
  @include flexWrap();
  align-items: start;

  .container-flex-wrap {
    @include flexWrap();

    width: 100%;
    align-items: center;
    justify-content: space-around;
    flex-wrap: wrap;
  }
}

.cards-btns {
  @include flexWrap();
  align-items: center;
  justify-content: flex-end;
  position: relative;
  height: 2em;

  .svg-btn {
    @include buttonStyle;
    margin-right: 10px;

    &:last-child {
      margin-right: 0;
    }
  }

  .delete-btn {
    &:hover {
      color: $delete-color;
    }
  }

  .edit-btn {
    &:hover {
      color: $background-color;
    }
  }

  .info-btn {
    color: $good-color;
    cursor: default;
    left: 0;
    position: absolute;
  }

  .copy-btn {
    &:hover {
      color: $good-color;
    }
  }
}

.info-btn {
  color: $good-color;
  cursor: default;
  margin: 0 5px;
}

::-webkit-scrollbar {
  width: 7px;
  height: 6px;
}

::-webkit-scrollbar-track {
  box-shadow: inset 0 0 5px grey;
  border-radius: 5px;
}

::-webkit-scrollbar-thumb {
  background: $thunck-color;
  border-radius: 5px;
}

::-webkit-scrollbar-thumb:hover {
  background: $thunck-color-hover;
}

.search-list__panel {
  position: sticky;
  top: 1rem;
  width: 30rem;
  @media (max-width: 850px) {
    position: relative;
    margin: 0 auto 40px;
    max-width: 100%;
    display: flex;
    flex-direction: column;
    align-items: center;
  }
}

.search-lesson-group {
  display: flex;
  align-items: center;
  align-content: center;

  .search-lesson {
    margin-left: -300px;
    margin-right: 110px;
  }
}

.language-icon {
  max-width: 30px;
  margin-right: 25px;
}

